import React from 'react'

const Movement = ({newName, setNewName, newSets, setNewSets, newReps, setNewReps, repWeight, setRepWeight, addMovement}) => {
    return(
        <>
        <h2 className="movementhead">Movement</h2>
        <div className="movementadd">
            <div className="movementnamecontainer">
                <p>Name</p>
                <input
                    placeholder="Squats...etc."
                    value={newName}
                    onChange={event => setNewName(event.target.value)}
                >
                </input>
            </div>
            <div className="setsrepscontainer">
                <p>No. Sets</p>
                <input
                    placeholder="1...5"
                    type="number"
                    min="1"
                    max="100"
                    value={newSets}
                    onChange={event => setNewSets(event.target.value)}
                >
                </input>
                <p>No. Reps</p>
                <input
                    placeholder="1...12"
                    type="number"
                    min="0"
                    max="100"
                    value={newReps}
                    onChange={event => setNewReps(event.target.value)}
                    >
                </input>
            </div>
            <div className="movementweightcontainer">
                <p>Weights</p>
                <input
                    placeholder="Separate with ;"
                    className="weightsinput"
                    value={repWeight}
                    onChange={event => setRepWeight(event.target.value)}
                    ></input>
            </div>
            <div className="savebutton">
            <button onClick={addMovement}>Save Movement</button>
            </div>
        </div>
        </>
    )
}

export default Movement