import React from 'react'
import {
    Link
 } from "react-router-dom"

const History = ({workouts}) => {
    return(
        <section className="historySection">
            <article className='workoutArticle'>
                <h2>Workout history</h2>
                <nav>
                {workouts.map((workout, indx) =>
                    <ul key={workout.id}>
                        <Link className="linktoworkout" to={`/${workout.id}`}>
                        <li>{indx + 1}</li>
                        <li>{workout.date.split('T')[0]}</li>
                        <li>{workout.theme}</li>
                        </Link>
                    </ul>
                    ).reverse()}
                </nav>
            </article>
        </section>
    )
}

export default History