import React from 'react'
import {
    Link
 } from "react-router-dom"
 
const Header = () => {
    return(
        <div>
        <h1 id="header">WorkoutTracker</h1>
        <ul className="headernavbar">
            <li><Link to={"/"}> History</Link></li>
            <li><Link to={"/add"}> Add Workout</Link></li>
        </ul>
        </div>
    )
}

export default Header