import React, { useState, useEffect } from 'react'
import {
    useParams, useNavigate
  } from "react-router-dom"
  import workoutService from '../services/workouts'

const Singleworkout = () => {
    const {id} = useParams()
    const [isLoading, setLoading] = useState(true);
    const [singleWorkout, setSingleWorkout] = useState([])

    let navigate = useNavigate()
    useEffect(()=>{
        workoutService
            .getOne(id)
            .then(oneWorkout => {
                setSingleWorkout(oneWorkout)
                setLoading(false)
            })
    },[])

    const handleRemoveClick = (id) => {
        const theme = singleWorkout.theme
        const date = singleWorkout.date.split('T')[0]
    
        if(window.confirm(`Delete ${theme} workout from ${date}?`)){
            workoutService
              .remove(id)
            console.log(`${theme} deleted`)
            navigate('/')
          }
    }

    if (isLoading) {
        return <div className="Loading">Loading...</div>;
      }
    

    return(
        <section className="singleWorkout"> 
            <article className='workoutArticle'>
                <div className="themeDate">
                    <h2>{singleWorkout.date.split('T')[0]}</h2>
                    <h2>{singleWorkout.theme}</h2>
                    <h2>{singleWorkout.duration}</h2>
                </div>
                        <h4 className='singleHead'>Movements</h4>
                    
                        {singleWorkout.movements.map((info,indx) =>
                            <ul key={indx} className="movementsUl">
                            <li id="movementName">{info.mname}</li>
                            <li id="movementSet">{`${info.msets}x${info.mreps}`}</li>
                            {info.mweights.map((weight, indx) => 
                                <div key={indx} className="weightsUl">
                                <li>{indx+1}</li>
                                <li>{`${weight}kg`}</li>
                                </div>
                                )}
                            </ul>
                         )}
                        {singleWorkout.cardio.length > 0 && (
                        <h4 className='singleHead'>Cardio</h4>)}
                        {singleWorkout.cardio.map((cardio,indx) => 
                            <ul className="cardioUl" key={indx}>
                            <li>{cardio.cname}</li>
                            <li className='cardioDuration'>{cardio.cduration}</li>
                            </ul>
                            )}
                        <div className="bottomButton">
                            <button
                            onClick={() => handleRemoveClick(singleWorkout.id)}>
                            Delete Workout
                            </button>
                        </div>
            </article>
        </section>
        )
    
}

export default Singleworkout