import React, { useState } from 'react';

import workoutService from '../services/workouts'
import {
     useNavigate
  } from "react-router-dom"
import Info from './Info'
import Movement from './Movement';
import Cardio from './Cardio';

const Template = () => {
const [fullWorkout, setFullWorkout] = useState([])
const [newTheme, setNewTheme] = useState('')
const [newDate, setNewDate] = useState('')
const [newDuration, setNewDuration] = useState('')

const [movement, setMovement] = useState([])
const [newName, setNewName] = useState('')
const [newSets, setNewSets] = useState('')
const [newReps, setNewReps] = useState('')
const [repWeight, setRepWeight] = useState('')

const [cardio, setCardio] = useState([])
const [cardioType, setCardioType] = useState('')
const [cardioDuration, setCardioDuration] = useState('')

let navigate = useNavigate()

const generateId = (state) => {
    const nextId = state.length > 0
    ? Math.max(...state.map(s => s.id))
    : 0
    return nextId + 1
}

const addMovement = (event) =>{
    event.preventDefault()
    let reps = (repWeight.split(";").map(Number))

    if(!newName || !newSets || !newReps){
        alert("Add movements name, sets and reps") 
    }else{
        const movementObject = {
            id: generateId(movement),
            mname: newName,
            mweights: reps,
            msets: Number(newSets),
            mreps: Number(newReps),
        }

        setMovement(movement.concat(movementObject))
        setNewName('')
        setNewSets('')
        setNewReps('')
        setRepWeight('')
        reps=0
    }
}

const addCardio = (event) => {
    event.preventDefault()

    if(!cardioType || !cardioDuration){
        alert("Add cardios name and duration")
    }else{
    const cardioObject = {
        id: generateId(cardio),
        cname: cardioType,
        cduration: cardioDuration
    }
    setCardio(cardio.concat(cardioObject))
    setCardioType('')
    setCardioDuration('')
}
}

const addFullWorkout = (event) => {
    event.preventDefault()

    console.log(newDate)
    if(!newTheme || !newDate || !newDuration){
        alert("Add workouts theme, date or duration")
    }else{
        const fullWorkoutObject = {
            ...fullWorkout,
            theme: newTheme,
            date: newDate,
            duration: newDuration,
            movements: movement,
            cardio: cardio,
        }
    
        workoutService
            .create(fullWorkoutObject)
            .then(returnedWorkout => {
                setFullWorkout(fullWorkout.concat(returnedWorkout))
                setNewTheme('')
                setNewDate('')
                setNewDuration('')
                navigate('/')
            })
    }
}

const deleteEntry = (entry,id) => {
    const entryToDelete = entry.find(item => item.id === id)
    const entryId = entryToDelete.id

    entry === movement ?
    setMovement(entry.filter(item => item.id !== entryId))
    : setCardio(entry.filter(item => item.id !== entryId))
}
    

    return (
        <section className="addWorkout">
            <article className='workoutArticle'>
                <Info 
                    newTheme={newTheme} 
                    setNewTheme={setNewTheme} 
                    newDate={newDate} 
                    setNewDate={setNewDate} 
                    newDuration={newDuration} 
                    setNewDuration={setNewDuration}
                />
                <Movement
                    newName={newName}
                    setNewName={setNewName}
                    newSets={newSets}
                    setNewSets={setNewSets}
                    newReps={newReps}
                    setNewReps={setNewReps}
                    repWeight={repWeight}
                    setRepWeight={setRepWeight}
                    addMovement={addMovement}
                />
                <Cardio
                    cardioType={cardioType}
                    setCardioType={setCardioType}
                    cardioDuration={cardioDuration}
                    setCardioDuration={setCardioDuration}
                    addCardio={addCardio}
                />
                <div>
                </div>
            </article>
            {movement.length > 0 && (
            <article className='workoutArticle'>
                <h2>Movements being added</h2>
                    {movement.map((item, indx) =>
                        <ul key={indx} className='movementaddedUl'>
                        <li>{item.mname}</li>
                        <li>{item.msets}x{item.mreps}</li>
                        <li>{`${item.mweights}kg `}</li>
                        <button className='deleteSinglebutton' onClick={() => deleteEntry(movement,item.id)}>Delete</button>
                        </ul>
                    )}
            {cardio.length > 0 && (
                <div>
                <h2>Cardio being added</h2>
                    {cardio.map((item, indx) => 
                        <ul key={indx} className='cardioaddedUl'>
                        <li>{item.cname}</li>
                        <li>{item.cduration}</li>
                        <button className='deleteSinglebutton' onClick={() => deleteEntry(cardio,item.id)}>Delete</button>
                        </ul>
                    )}
                </div>
            )}
                <div className='bottomButton'>
                    <button onClick={addFullWorkout}>Save this workout</button>
                </div>
            </article>
            )}
        </section>
    )

}

export default Template