import React from 'react'

const Cardio = ({cardioType, setCardioType, cardioDuration, setCardioDuration, addCardio}) => {
    return(
        <>
        <h2 className="cardioheader">Cardio</h2>
        <div className="cardioadd">
            <div>
                <p>Name</p>
                <input
                    placeholder="Row...etc."
                    value={cardioType}
                    onChange={event => setCardioType(event.target.value)}
                >
                </input>
                <p>Duration | h:min:s</p>
                <input
                    value={cardioDuration}
                    onChange={event => setCardioDuration(event.target.value)}
                >
                </input>
            </div>
            <div className="savebutton">
                <button onClick={addCardio}>Save Cardio</button>
            </div>
        </div>
        </>
    )
}

export default Cardio